import React, { useState, useEffect } from 'react';
import Web3 from 'web3';


const App = () => {
  const [otp, setOtp] = useState('');
  const [isVerified, setIsVerified] = useState(false);
  const [contract, setContract] = useState(null);

  useEffect(() => {
    const initWeb3AndContract = async () => {
      console.log('initWeb3AndContract called');
      try {
        // Initialize Web3 instance
        const web3 = new Web3(Web3.givenProvider || 'http://localhost:8545');
        
      
        // Fetch contract JSON
        const response = await fetch('/build/contracts/OTPContract.json');
        
        const responseBody = await response.text();
        console.log('response body:', responseBody);
        
        console.log('fetch response:', response); // Add this line
        if (!response.ok) {
          throw new Error('Failed to load contract JSON');
        }

        const contractJSON = await response.json();
        console.log('contractJSON:', contractJSON);
        const networkId = await web3.eth.net.getId();
        console.log("Network ID:", networkId);
        const deployedNetwork = contractJSON.networks[networkId];

        // Check if contract is deployed on the network
        if (!deployedNetwork) {
          throw new Error('Smart contract not deployed to detected network');
        }

        // Instantiate contract
        const otpContract = new web3.eth.Contract(
          contractJSON.abi,
          deployedNetwork.address
        );

        setContract(otpContract); // Set the contract state if needed
      } catch (error) {
        console.error('Error loading Web3 or contract:', error);
      }
      
    };

    initWeb3AndContract();
  }, []);

  const generateOtp = async () => {
    try {
      const otp = await contract.methods.generateOTP().call();
      setOtp(otp);
    } catch (error) {
      console.error('Error generating OTP:', error);
    }
  };

  const verifyOtp = async (otp) => {
    try {
      const result = await contract.methods.verifyOTP(otp).call();
      setIsVerified(result);
    } catch (error) {
      console.error('Error verifying OTP:', error);
    }
  };

  useEffect(() => {
    if (contract) {
      generateOtp();
    }
  }, [contract]);

  const handleVerifyOtp = () => {
    verifyOtp(otp);
  };

  
};

export default App;
